import { client } from '@/lib/sanity'
import Link from 'next/link'
import { notFound } from 'next/navigation'

// 단일 아티클을 슬러그로 조회
const articleBySlugQuery = `
*[_type == "article" && slug.current == $slug][0]{
  _id,
  title,
  "slug": slug.current,
  author,
  body,
  content
}`

export default async function ArticlePage({ params }: { params: { slug: string } }) {
  const { slug } = params
  const article = await client.fetch(articleBySlugQuery, { slug })

  if (!article) {
    notFound()
  }

  return (
    <div className="min-h-screen bg-white flex">
      <main className="flex-1 p-12 lg:p-20 max-w-3xl">
        {/* 상단 네비게이션 */}
        <div className="mb-12">
          <Link href="/" className="text-sm text-gray-600 hover:text-black">
            ← 홈으로
          </Link>
        </div>

        {/* 아티클 제목/메타 */}
        <h1 className="text-4xl font-bold mb-2">{article.title}</h1>
        {article.author && (
          <p className="text-gray-600 mb-8">{article.author}</p>
        )}

        {/* 본문 (Portable Text 렌더러가 없을 때의 안전한 fallback) */}
        {Array.isArray(article.body) ? (
          <div className="prose">
            {article.body.map((block: any, i: number) => (
              <p key={i}>{block?.children?.map((c: any) => c.text).join('')}</p>
            ))}
          </div>
        ) : (
          <p className="whitespace-pre-wrap">{article.body || article.content || ''}</p>
        )}
      </main>
    </div>
  )
}